<template>
    <div>
      <div class="seeBox">
      <searchBox ref="searchBox" @query="onQueryChange" @shows="shows" :offShow="offShow" @onshows="onshows"></searchBox>
      </div>
      <div class="shortcutWrapper" v-show="offShow&&!query" ref="shortcutWrapper">
        <scroll :data="shortcut" class="wrapper" ref="wrapper" :refreshDelay="refreshDelay">
          <div class="padding">
        <div class="shortcut">
          <h3>热门搜索</h3>
          <div>
            <ul  class="hotKey">
              <li class="item" @click='addQuery(item.k)' v-for="(item,index) in hotKey" :class="getIndex(index)">
                {{item.k}}
              </li>
            </ul>
          </div>
        </div>
        <div class="searchHistory" v-show="searchHistory.length">
          <h1 class="title">
            <span class="text">搜索历史</span>
            <span class="clear" @click="showConfirm">
              <i class="iconfont icon-shanchu"></i>
            </span>
          </h1>
          <searchList :searches="searchHistory" @select="addQuery" @delete="deleteOne"></searchList>
        </div>
          </div>
        </scroll>
      </div>
      <div class="searchBox" v-show="query" ref="searchResult">
      <Suggest :query="query" @listScroll="blurInput" @select="saveSearch" ref="suggest"></Suggest>
      </div>
      <confirm ref="confirm" text="是否清空所有搜索记录" confirmBtnText="清空" @confirm="clearSearchHistory"></confirm>
      <router-view></router-view>
    </div>

</template>

<script>
  import searchBox from '../../base/searchBox'
  import {getHotKey} from "../../api/search";
  import {ERR_OK} from "../../api/config";
  import Suggest from '../../components/suggest/suggest'
  import {mapActions,mapGetters} from 'vuex'
  import searchList from '../../base/searchList'
  import confirm from '../../base/confirm'
  import scroll from '../../base/scroll'
  import {playlistMixin} from '../../common/js/mixin'
  export default {
    mixins:[playlistMixin],
      name: "search",
      data(){
        return{
          hotKey:[],
          query:'',
          offShow:true,
          refreshDelay:100
        }
      },
      components:{
          searchBox,
          Suggest,
        searchList,
        confirm,
        scroll
      },
    computed:{
      shortcut(){
        return this.hotKey.concat(this.searchHistory)
      },
      ...mapGetters([
        'searchHistory'
      ])
    },
      created(){
          this._getHotKey()
      },
      methods:{
        _getHotKey(){
            getHotKey().then((res)=>{
              if(res.code===ERR_OK){
                  this.hotKey=res.data.hotkey.slice(0,9)
              }
            })
          },
        getIndex(index){
          if(index===0){
            return 'icon'
          }
        },
        addQuery(query){
          this.$refs.searchBox.setQuery(query)
        },
        onQueryChange(query){
          this.query=query
        },
        shows(onshow){
          this.offShow=onshow
        },
        onshows(onshows){
          this.offShow=onshows
        },
        blurInput(){
          this.$refs.searchBox.blur()
        },
        saveSearch(){
          this.saveSearchHistory(this.query)
        },
        deleteOne(item){
          this.deleteSearchHistory(item)
        },
        showConfirm(){
          this.$refs.confirm.show()
        },
        handlePlayList(playList){
          const bottom =playList.length>0 ? '66px':''
          this.$refs.shortcutWrapper.style.bottom = bottom
          this.$refs.searchResult.style.bottom = bottom
          this.$refs.wrapper.refresh()
          this.$refs.suggest.refresh()
        },
        ...mapActions([
          'saveSearchHistory',
          'deleteSearchHistory',
          'clearSearchHistory'
        ])
      },
    watch:{
        query(newQuery){
          if(!newQuery){
            setTimeout(()=>{
              this.$refs.wrapper.refresh()
            },20)
          }
        }
    }

    }
</script>

<style scoped lang="stylus">
  .searchBox{
    position:fixed
    top:144px
    width:100%
    bottom:0
  }
  .shortcutWrapper{
    position: fixed
    top:144px
    width :100%
    bottom:0
    background: #fff;
    padding: 15px 15px 10px 15px;
    overflow :hidden
    .shortcut{
      width:100%
      h3{
        color: rgba(0,0,0,.6);
        margin-bottom: 15px;
        font-size :14px
      }
      .hotKey{
        display:flex
        flex-direction :row
        flex-wrap :wrap
        align-items :center

        .item{
          font-size: 15px;
          padding: 0 10px;
          height: 30px;
          line-height: 30px;
          color: #000;
          box-sizing :border-box
          border: 1px solid rgba(0,0,0,.6);
          border-radius: 99px;
          word-break: keep-all;
          margin-bottom: 10px;
          margin-right: 16px;
        }
        .icon{
          border-color:#FC4524
          color:#FC4524
        }
      }
    }
    .searchHistory{
      margin-top:10px
      width:100%
      margin-right:40px
      .title{
         display:flex
         align-items :center
         color: rgba(0,0,0,.6);
         margin-bottom: 8px;
         font-size :14px
         height:20px
         line-height :20px
        .text{
          flex:1
        }
        .clear{
          flex:0 0 40px
        }
       }

    }
  }
  .wrapper{
    position: absolute
    top:0
    bottom:0
    padding-top:10px
    .padding{
      padding-top:10px
    }
  }


</style>



